import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import './App.css'
import Home from './panels/Home';
import Persik from './panels/Persik';
import Epic from "@vkontakte/vkui/dist/components/Epic/Epic";
import Tabbar from '@vkontakte/vkui/dist/components/Tabbar/Tabbar';
import TabbarItem from '@vkontakte/vkui/dist/components/TabbarItem/TabbarItem';
import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';
import Icon28More from '@vkontakte/icons/dist/28/more';
import Posts from "./panels/Posts";
import Сatalog from "./panels/Сatalog";
import PhotoPreview from "./panels/PhotoPreview";
import ModalCard from "@vkontakte/vkui/dist/components/ModalCard/ModalCard";
import ModalRoot from "@vkontakte/vkui/dist/components/ModalRoot/ModalRoot";
import Avatar from "@vkontakte/vkui/dist/components/Avatar/Avatar";

const App = () => {
	const [activeView, setActiveView] = useState('post');
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const [userArr,setUserArr] = useState([])
	const [lang,setLang] = useState('ru')
	const [postArr,setPostArr] = useState([])
	const [offset,setOffset] = useState(0)
	const [photo, setPhoto] = useState('')
	const [activeModal, setActiveModal] = useState('subscription')


	useEffect(() => {
		const urlParams = new URLSearchParams(window.location.search);
		setLang(urlParams.get('vk_language' || "ru"))
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
			if (type === 'VKWebAppCallAPIMethodResult' && data.request_id === "getUsers") {
				setPopout(null)
				setUserArr(data.response)
			}
			if (type === 'VKWebAppCallAPIMethodResult' && data.request_id === "getPosts") {
				setPopout(null)
				setPostArr(data.response.items)
				setOffset(offset + 50)
				console.log(data.response.items)
			}
		});
		async function fetchData() {
			const user = await connect.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
		getPosts()
	}, []);
	useEffect(() => {
	}, []);



	const addGroup = () => {
		// connect.send("VKWebAppAllowMessagesFromGroup", {"group_id": 185650440, "key": "dBuBKe1kFcdemzB"});
		connect.send("VKWebAppJoinGroup", {"group_id": 185650440});
	}

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	const showPhoto = (photo) => {
		setPhoto(photo)
		setActivePanel('photo')
	}

	const getPosts = (offset = '') => {
		connect.send("VKWebAppCallAPIMethod", {
			"method": "wall.get",
			"request_id": "getPosts",
			"params": {
				"filters": 'post',
				"owner_id": '-113161053',
				"count": 50,
				"v": "5.105",
				// "access_token": '90367c6c798c106c4e98663bed2148d2dd86964270a75b329668efd46b8aa051b8aab8d98dceb5bbd8d5a',
				"access_token": '85685eaa85685eaa85685eaa2d8504b3b58856885685eaad8e8ae5c720f4db08e7e9ee0',
				'lang' : lang,
				'offset': offset
			}
		});
	}


	const modal = (
		<ModalRoot activeModal={activeModal}>
			<ModalCard
				id='subscription'
				onClose={() => setActiveModal(null)}
				icon={<Avatar src="https://sun1-25.userapi.com/c857616/v857616788/170f9b/EKExqwLJWgs.jpg" size={72} />}
				caption={<span><h1>Мои рисунки, наброски | Бот </h1>Мини приложение предлагает Вам <br/> подписаться на сообщество</span>}
				actions={[{
					title: 'Подписаться',
					mode: 'primary',
					action: () => {
						addGroup()
						setActiveModal(null)
					}
				}]}
				actionsLayout="vertical"
			>
			</ModalCard>
		</ModalRoot>
	);

	return (
		<Epic activeStory={activeView} tabbar={
			<Tabbar>
				<TabbarItem
					onClick={() => setActiveView('post')}
					selected={activeView === 'post'}
					// text="Новости"
				><Icon28Newsfeed /></TabbarItem>
				<TabbarItem
					onClick={() => setActiveView('catalog')}
					selected={activeView === 'catalog'}
					// text="Каталог художников"
				><Icon28More /></TabbarItem>
			</Tabbar>
		}>
			<View activePanel={activePanel} popout={popout} id="post" modal={modal}>
				<Posts id='home' fetchedUser={fetchedUser} go={go} setPopout={setPopout} lang={lang} postArr={postArr} offset={offset}
					   showPhoto={showPhoto} getPosts={getPosts}/>
				<PhotoPreview id='photo' go={go} photo={photo}/>
			</View>
			<View activePanel={'people'} popout={popout} id="catalog">
				<Сatalog setPopout={setPopout} id='people' fetchedUser={fetchedUser} go={go} userArr={userArr} lang={lang}/>
			</View>
		</Epic>
	);
}

export default App;

